<template>
  <div class='payment-box'>
    
    <van-notice-bar :scrollable="false">
      订单已生成！请在10分钟内({{time}})完成付款
    </van-notice-bar>
    <div class="payinformation">
      <p class="pay-item">
        <span class="sp1">项目名称</span>
        <span class="sp2">挂号支付</span>
      </p>
      <p class="pay-item">
        <span class="sp1">支付金额</span>
        <span class="sp2">￥<span>0</span></span>
      </p>
      <p class="pay-item">
        <span class="sp1">就诊卡</span>
        <span class="sp2">123123</span>
      </p>
      <p class="pay-item">
        <span class="sp1">姓名</span>
        <span class="sp2">周小森</span>
      </p>
    </div>
    <div class="wechat-box">
      <div class="img-text">
        <img src="../../../assets/img/wechat.png"/>
        <span>微信支付</span>
      </div>
      <van-icon name="arrow"  class="rightarrow mr-10"/>
    </div>
  </div>
</template>

<script>
  export default {
    name: '',
    data(){
      return{
        time:  "10:00"
      }
    },
    mounted(){
      this.contDown()
    },
    methods:{
      contDown(){
        let that = this
        var countdownMinute = 10;//10分钟倒计时 
        var startTimes = new Date();//开始时间 new Date('2016-11-16 15:21'); 
        var endTimes = new Date(startTimes.setMinutes(startTimes.getMinutes()+countdownMinute));//结束时间 
        var curTimes = new Date();//当前时间 
        var surplusTimes = endTimes.getTime()/1000 - curTimes.getTime()/1000;//结束毫秒-开始毫秒=剩余倒计时间 
        // 进入倒计时 
        var countdowns = window.setInterval(function(){ 
          surplusTimes--; 
          var minu = Math.floor(surplusTimes/60); 
          var secd = Math.round(surplusTimes%60); 
          that.time = minu+':'+secd
          if(surplusTimes<=0){ 
            console.log('时间到！'); 
            clearInterval(countdowns); 
          } 
        },1000); 
      }
    }
  };
</script>
<style lang='scss' scoped>
.wechat-box{
    display: flex;
    justify-content: space-between;
    align-items:center;/*垂直居中*/
    background: #fff;
    padding: 10px 0;
    margin-top: 10px;
    .img-text{
      img{
        width: 2rem;
        height: 2rem;
        margin-left: 10px;
      }
    }
    .rightarrow{color: #999;}
  }
.payment-box{
  .payinformation{
    background: #fff;
    p.pay-item{
      padding: 10px;
      position: relative;
      display: flex;
      justify-content: space-between;
      .sp1{
        color: #999
      }
      .sp2{
        span{color: #FFB30B}
      }
    }
    p.pay-item::after {
    position: absolute;
    box-sizing: border-box;
    content: ' ';
    pointer-events: none;
    right: 0;
    bottom: 0;
    left: 0px;
    border-bottom: 1px solid #ddd;
    -webkit-transform: scaleY(.5);
    transform: scaleY(.5);
}
  }
}
</style>


